.widescreen_slider {
  display: block;
  padding: 0px;
  position: relative;
}
.widescreen_slider .owl-carousel {
  width: calc(100% + 65px);
  max-width: calc(100% + 65px);
  margin: 0 -33px;
}
.widescreen_slider.for-category .owl-carousel {
  width: 100%;
  max-width: 100%;
  margin: 0;
}
.widescreen_slider.for-category {
    margin: 0 25px 23px 0;
}
.widescreen_slider .owl-carousel .owl-stage {
  max-width: initial !important;
}
.widescreen_slider a {
  display: block;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  text-decoration: none;
  border: 0px;
}
.widescreen_slider a img {
  display: block;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0;
}
.widescreen_slider .owl-nav {
  position: absolute;
  height: 0px;
  overflow: visible;
  top: 50%;
  z-index: 1;
  left: 0;
  right: 0;
}
.widescreen_slider .owl-nav button {
  position: absolute;
  z-index: 5;
  top: -24px;
  width: 48px;
  height: 48px;
  border-radius: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.35) !important;
  text-align: center;
}
.widescreen_slider .owl-nav button span {
  display: inline-block;
  vertical-align: middle;
  color: transparent !important;
  width: 26px;
  height: 25px;
  background-color: transparent !important;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-color: transparent;
  background-size: 100% auto;
}
.widescreen_slider .owl-nav button.owl-prev {
  left: 19px;
}
.widescreen_slider .owl-nav button.owl-prev span {
  background-image: url('../images/svg/arrow_left_gray.svg');
  margin-left: 6px;
}
.widescreen_slider .owl-nav button.owl-prev:hover span {
  background-image: url('../images/svg/arrow_left_green.svg');
}
.widescreen_slider .owl-nav button.owl-next {
  right: 19px;
}
.widescreen_slider .owl-nav button.owl-next span {
  background-image: url('../images/svg/arrow_right_gray.svg');
  margin-right: 6px;
}
.widescreen_slider .owl-nav button.owl-next:hover span {
  background-image: url('../images/svg/arrow_right_green.svg');
}
.widescreen_slider .owl-dots {
  position: absolute;
  bottom: 10px;
  z-index: 3;
  left: 0;
  right: 0;
  text-align: center;
}
.widescreen_slider .owl-dots .owl-dot {
  display: inline-block;
  vertical-align: top;
  margin: 0 5px 5px;
  padding: 0;
}
.widescreen_slider .owl-dots .owl-dot span {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 100%;
  overflow: hidden;
  border: 1px solid #87CD00;
  background-color: transparent;
  color: transparent;
}
.widescreen_slider .owl-dots .owl-dot.active span,
.widescreen_slider .owl-dots .owl-dot:hover span {
  border: 1px solid #87CD00;
  background-color: #87CD00;
}
@media (min-width: 901px) and (max-width: 1199px) {
  .widescreen_slider .owl-carousel {
    width: calc(100% + 26px);
    max-width: calc(100% + 26px);
    margin: 0 -13px;
  }
}
@media (min-width: 0px) and (max-width: 900px) {
  .widescreen_slider .owl-carousel {
    width: calc(100% + 26px);
    max-width: calc(100% + 26px);
    margin: 0 -13px;
  }
}
@media (min-width: 481px) and (max-width: 900px) {
  #header {
    margin: 0 !important;
  }
}
@media (max-width: 480px) {
  #header {
    margin: 0 !important;
  }
}
@media (max-width: 640px) {
  .widescreen_slider .owl-nav button {
    top: -16px;
    width: 32px;
    height: 32px;
  }
  .widescreen_slider .owl-nav button span {
    width: 16px;
    height: 15px;
  }
  .widescreen_slider .owl-nav button.owl-prev {
    left: 15px;
  }
  .widescreen_slider .owl-nav button.owl-prev span {
    margin-left: 1px;
    margin-top: -3px;
  }
  .widescreen_slider .owl-nav button.owl-next {
    right: 15px;
  }
  .widescreen_slider .owl-nav button.owl-next span {
    margin-right: 4px;
    margin-top: -3px;
  }
  .widescreen_slider .owl-dots .owl-dot {
    margin: 0 5px 5px;
  }
  .widescreen_slider .owl-dots .owl-dot span {
    width: 10px;
    height: 10px;
  }
}

@media  (max-width:1199px) {
    .widescreen_slider.for-category {
        margin-right: 0;
    }
}